.AddTask {
    background-color: rgb(27, 230, 105);
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.AddTask .circle {
    background-color: white;
    height: 20px;
    width: 20px;
    border-radius: 10px;
    border: 2px solid blue;
}

.AddTask #text {
    height: 30px;
    flex-basis: 80%;
    outline: none;
    border: none;
    background-color: rgb(27, 230, 105);
    font-size: 1.1rem;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: 450;
}


.AddTask #text::placeholder {
    padding: 1px;
    color: black;
    font-size: 1.1rem;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.AddTask .AddText {
    height: 30px;
    padding: 4px;
    border-color: blue;
    border-radius: 7px;
    cursor: pointer;
}