.Header {
    color: white;
    background-color: blue;
    padding: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-radius: 10px 10px 0 0;
}

.Header h1 {
    margin: auto 10px;
}

.Header div {
    margin-right: 10px;
    padding: 10px;
    background-color: red;
    border-radius: 100px;
}