.TaskList {
    list-style: none;
    padding: auto 18px;
    background-color: aquamarine;
    font-size: 1.4rem;
    font-weight: 450;
    font-family: 'Courier New', Courier, monospace;
    border-radius: 0 0 10px 10px;
}

.TaskList li {
    color: black;
}

.TaskList li > span {
    float: right;
}

.listElem {
    padding: 5px;
    margin: 5px auto;
    background-color: rgb(158, 158, 158);
    border-radius: 0 0 7px 7px;
}

.listElem #delt {
    cursor: pointer;
    padding: auto 3px;
    margin: auto 5px;
}
